.agenda-container{
    padding-top: 90px;
}

.funcionamento{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 30px;
}
.funcionamento li{
    list-style: none;
    max-width: 300px;
    padding: 10px 20px;
}
.funcionamento li img{
    width: 150px;
    height: 150px;
    border-radius: 75px;
}
.confirm{
    color: green;
}
.agendarconsulta{
    background-color: #494ffe;
    width: 50%;
    margin-left: 350px;
    color:  #fff;
    font-size: 16px;
    border-radius: 10px;
    padding: 10px;
}
